import React from "react";
import styled from "styled-components";
import {
  Grid as GridContainer,
  GridSection,
  TopBar,
  GridItem,
  Footer
} from "./Grid";

const ParagraphList = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  > p.list-item{
    padding: 1.5em 0;
    border-bottom: 1px solid #aaaaaa;
    margin: 0 auto !important;
    line-height: 1;
    font-size: 0.75em;
    font-weight: 600;
    text-transform: uppercase;
    width: calc(49% - 40px);
    &:first-of-type, &.top{
      border-top: 1px solid #bababa;
    }
  }
`;
const Home = () => (
  <div>
    <TopBar>Things I Can Do</TopBar>
    <ParagraphList>
      <p className="list-item">Javascript/ES6</p>
      <p className="list-item top">JQuery</p>
      <p className="list-item">HTML</p>
      <p className="list-item">CSS/SASS/Styled Components</p>
      <p className="list-item">React</p>
      <p className="list-item">Redux</p>
      <p className="list-item">Ruby on Rails</p>
      <p className="list-item">Node</p>
    </ParagraphList>

  </div>
);

export default Home;



